import React from "react";
import ModuleList from "./ModuleList";
import LessonTabsComponent from "./LessonTabsComponent";
import {Link} from "react-router-dom";
import "../style/CourseEditorComponentStyle.css"
import TopicPills from "./TopicPillsComponent";
import WidgetListComponent from "./WidgetListComponent";
import SavePreviewWidgetComponent from "./SavePreviewWidgetComponent";
import AddWidgetComponent from "./AddWidgetComponent";

// stateless component
const CourseEditorComponent = () => {
    return (
        <div>
            <div className={"row wbdv-editor-header"}>
                <div className={"col-1"}>
                    <Link to="/table/courses">
                        <button className={"btn wbdv-editor-back"}>
                            <i className={"fa fa-times"}/>
                        </button>
                    </Link>
                </div>

                <div className={"col-3"}>
                    <h4>Course Editor</h4>
                </div>

                <div className={"col-7"}>
                    <LessonTabsComponent/>
                </div>

                <div className={"col-1"}>
                    <button className={"btn float-right wbdv-editor-add-lesson"}>
                        <i className={"fa fa-plus"}/>
                    </button>
                </div>
            </div>


            <div className="row">
                    <ModuleList/>

                <div className="col-8">

                    <TopicPills/>
                    <br/>
                    <SavePreviewWidgetComponent/>
                    <br/>
                    <WidgetListComponent/>
                    <br/>
                    <AddWidgetComponent/>
                    <br/>

                </div>
            </div>
        </div>
    )
}

export default CourseEditorComponent
