import React from "react";

const ModuleList = () =>
    <div className="col-4 wbdv-module-list-div">
        <div className="wbdv-module-section">
            <ul className="list-group">
                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile active">
                        Module 1 - jQuery
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile">
                        Module 2 - React
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile">
                        Module 3 - Redux
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile">
                        Module 4 - Native
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile">
                        Module 5 - Angular
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile">
                        Module 6 - Node
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

                <div className="row wbdv-module-item">
                    <li className="list-group-item wbdv-module-item-tile">
                        Module 7 - Mongo
                        <button className={"btn wbdv-delete-module float-right"}>
                            <i className="fa fa-times "/>
                        </button>
                    </li>
                </div>

            </ul>

            <button className="btn float-right wbdv-module-item-add-btn">
                <i className="fa fa-plus"/>
            </button>

        </div>
    </div>

export default ModuleList
